.loader {
  width: 130px;
  height: 48px;
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, 0);
  background-image: linear-gradient(45deg, #000 25%, rgba(0, 0, 0, 0) 25%, rgba(0, 0, 0, 0) 75%, #000 75%, #000 100%), linear-gradient(45deg, #000 25%, #fff 25%, #fff 75%, #000 75%, #000 100%);
  background-position: 0 0, 16px 16px;
  background-repeat: repeat, repeat;
  background-size: 32px 32px;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  font-size: 10px;
  animation: raceBoard .6s linear infinite;
  display: inline-block;
}

@keyframes raceBoard {
  0% {
    background-position: 0 0, 16px 16px;
  }

  100% {
    background-position: 32px 0, 48px 16px;
  }
}

